<template>
  <v-dialog
    v-model="Items.dialogOpen"
    persistent
    max-width="600px">
    <v-btn
      slot="activator"
      small
      color="deep-orange darken-1">{{ $ln(`新增${levelName}`) }}</v-btn>

    <v-card>
      <v-card-title>
        <span class="headline">{{ $ln(`新增${levelName}`) }}</span>
      </v-card-title>

      <v-card-text>
        <v-container grid-list-md>
          <v-form
            ref="form"
            v-model="Items.valid">
            <v-layout wrap>
              <v-flex
                xs12
                sm6
                >
                <v-select
                  v-model="Items.account.selected"
                  :items="Items.account.options"
                  :no-data-text="$ln('無可用帳號')"
                  :label="$ln(`${levelName}帳號`)"
                  required
                />
              </v-flex>

              <v-flex
                xs12
                sm6
                >
                <v-text-field
                  v-model="Items.nameItems.name"
                  :rules="Items.nameItems.nameRules"
                  :label="$ln(`${levelName}名稱`)"/>
              </v-flex>

              <v-flex
                xs12
                sm6
                >
                <v-text-field
                  v-model="Items.passwordItems.password"
                  :rules="Items.passwordItems.passwordRules"
                  :append-icon="Items.passwordItems.passwordIsVisible ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="Items.passwordItems.passwordIsVisible ? 'text' : 'password'"
                  :label="$ln('密碼')"
                  :hint="$ln('長度需為 4 位以上，並且是字母和數字的組合')"
                  required
                  @click:append="Items.passwordItems.passwordIsVisible = !Items.passwordItems.passwordIsVisible"
                />
              </v-flex>

              <v-flex
                xs12
                sm6
                >
                <v-text-field
                  v-model="Items.passwordItems.passwordConfirmation"
                  :rules="Items.passwordItems.passwordRules"
                  :append-icon="Items.passwordItems.passwordConfirmationIsVisible ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="Items.passwordItems.passwordConfirmationIsVisible ? 'text' : 'password'"
                  :label="$ln('確認密碼')"
                  :hint="$ln('長度需為 4 位以上，並且是字母和數字的組合')"
                  required
                  @click:append="Items.passwordItems.passwordConfirmationIsVisible = !Items.passwordItems.passwordConfirmationIsVisible"
                />
                <div class="v-text-field__details">
                  <div class="v-messages theme--light error--text">
                    <div class="v-messages__wrapper">
                      {{vaildPassword}}
                    </div>
                  </div>
                </div>
              </v-flex>

              <v-flex
                xs12
                sm6
                >
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label theme--light"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('是否允許登入') }}</label>
                    <v-radio-group
                      v-model="Items.whetherAllowLogin.selected"
                      :disabled="Items.whetherAllowLogin.disabled"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.whetherAllowLogin.options"
                        :key="i"
                        :label="$ln(item.text)"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <v-flex
                xs12
                sm6
                >
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('是否允許下注') }}</label>
                    <v-radio-group
                      v-model="Items.whetherAllowBet.selected"
                      :disabled="Items.whetherAllowBet.disabled"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.whetherAllowBet.options"
                        :key="i"
                        :label="$ln(item.text)"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>
            </v-layout>

            <v-layout wrap>
              <!-- 開放BingoStar -->
              <v-flex
                v-if="Items.openBingo.show"
                xs12
                sm4
                md4>
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('開放BingoStar') }}</label>
                    <v-radio-group
                      v-model="Items.openBingo.selected"
                      :disabled="Items.openBingo.disabled"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.openBingo.options"
                        :key="i"
                        :label="$ln(item.text)"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <!-- 開放Super彩球 -->
              <v-flex
                v-if="Items.openSuperLottery.show"
                xs12
                sm4
                md4>
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('開放Super彩球') }}</label>
                    <v-radio-group
                      v-model="Items.openSuperLottery.selected"
                      :disabled="Items.openSuperLottery.disabled"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.openSuperLottery.options"
                        :key="i"
                        :label="$ln(item.text)"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <!-- 開放真人(皇家)類別 -->
              <v-flex
                v-if="Items.openRoyal.show"
                xs12
                sm4
                md4>
                <div class="v-input__slot">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label"
                      style="left: 0px; right: auto; position: absolute;">{{ $ln('開放真人(皇家)類別') }}</label>
                    <v-radio-group
                      v-model="Items.openRoyal.selected"
                      :disabled="Items.openRoyal.disabled"
                      row>
                      <v-radio
                        class="v-input-checkbox"
                        v-for="(item, i) in Items.openRoyal.options"
                        :key="i"
                        :label="$ln(item.text)"
                        :value="item.value"/>
                    </v-radio-group>
                  </div>
                </div>
              </v-flex>

              <!-- 信用版 -->
              <!-- (體育)信用額度 -->
              <v-flex
                v-if="Items.sportCreditItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-model.number="Items.sportCreditItems.amount"
                  :rules="Items.sportQuotaRules"
                  :suffix="`${Items.sportCreditItems.suffixLeft}${Items.sportSurplusQuota}${Items.sportCreditItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :disabled="Items.sportCreditItems.disabled"
                  class="v-input-input"
                  :label="$ln('(體育)信用額度')"/>
              </v-flex>

              <!-- (BingoStar)信用額度 -->
              <v-flex
                v-if="Items.bingoCreditItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-if="Items.bingoCreditItems.noOpenText === '' && !Items.bingoCreditItems.disabled"
                  v-model.number="Items.bingoCreditItems.amount"
                  :rules="Items.bingoQuotaRules"
                  :suffix="`${Items.bingoCreditItems.suffixLeft}${Items.bingoSurplusQuota}${Items.bingoCreditItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :label="$ln('(BingoStar)信用額度')"
                  class="v-input-input"
                />
                <!-- <v-text-field
                  v-else-if="Items.bingoCreditItems.noOpenText === '' && Items.bingoCreditItems.disabled"
                  v-model="Items.bingoCreditItems.promptText"
                  :suffix="`${Items.bingoCreditItems.suffixLeft}${Items.bingoSurplusQuota}${Items.bingoCreditItems.suffixRight}`"
                  disabled
                  :label="$ln('(BingoStar)信用額度')"
                  class="v-input-input"
                /> -->
                <div
                  v-else-if="Items.bingoCreditItems.noOpenText === '' && Items.bingoCreditItems.disabled"
                  class="royalPrompt">
                  <label class="amountLabel">{{ $ln('(BingoStar)信用額度') }}</label>
                  <div class="amountMessage">{{ Items.bingoCreditItems.promptText }}</div>
                </div>
                <v-text-field
                  v-else
                  :placeholder="$ln(Items.bingoCreditItems.noOpenText)"
                  disabled
                  :label="$ln('(BingoStar)信用額度')"
                  class="v-input-input"
                />
              </v-flex>

              <!-- (皇家)信用額度 -->
              <v-flex
                v-if="Items.royalCreditItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-if="Items.royalCreditItems.noOpenText === '' && !Items.royalCreditItems.disabled"
                  v-model.number="Items.royalCreditItems.amount"
                  :rules="Items.royalQuotaRules"
                  :suffix="`${Items.royalCreditItems.suffixLeft}${Items.royalSurplusQuota}${Items.royalCreditItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :label="$ln('(皇家)信用額度')"
                  class="v-input-input"
                />
                <!-- <v-text-field
                  v-else-if="Items.royalCreditItems.noOpenText === '' && Items.royalCreditItems.disabled"
                  v-model="Items.royalCreditItems.promptText"
                  :suffix="`${Items.royalCreditItems.suffixLeft}${Items.royalSurplusQuota}${Items.royalCreditItems.suffixRight}`"
                  disabled
                  :label="$ln('(皇家)信用額度')"
                  class="v-input-input"
                /> -->
                <div
                  v-else-if="Items.royalCreditItems.noOpenText === '' && Items.royalCreditItems.disabled"
                  class="royalPrompt">
                  <label class="amountLabel">{{ $ln('(皇家)信用額度') }}</label>
                  <div class="amountMessage">{{ Items.royalCreditItems.promptText }}</div>
                </div>
                <v-text-field
                  v-else
                  :placeholder="$ln(Items.royalCreditItems.noOpenText)"
                  disabled
                  :label="$ln('(皇家)信用額度')"
                  class="v-input-input"
                />
              </v-flex>

              <!-- 現金版 -->
              <!-- (體育)現金額度 -->
              <v-flex
                v-if="Items.sportCashItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-model.number="Items.sportCashItems.amount"
                  :rules="Items.sportQuotaRules"
                  :suffix="`${Items.sportCashItems.suffixLeft}${Items.sportSurplusQuota}${Items.sportCashItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :disabled="Items.sportCashItems.disabled"
                  :label="$ln('(體育)現金額度')"
                  class="v-input-input"/>
              </v-flex>

              <!-- (BingoStar)現金額度 -->
              <v-flex
                v-if="Items.bingoCashItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-if="Items.bingoCashItems.noOpenText === '' && !Items.bingoCashItems.disabled"
                  v-model.number="Items.bingoCashItems.amount"
                  :rules="Items.bingoQuotaRules"
                  :suffix="`${Items.bingoCashItems.suffixLeft}${Items.bingoSurplusQuota}${Items.bingoCashItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :label="$ln('(BingoStar)現金額度')"
                  class="v-input-input"
                />
                <!-- <v-text-field
                  v-else-if="Items.bingoCashItems.noOpenText === '' && Items.bingoCashItems.disabled"
                  v-model="Items.bingoCashItems.promptText"
                  :suffix="`${Items.bingoCashItems.suffixLeft}${Items.bingoSurplusQuota}${Items.bingoCashItems.suffixRight}`"
                  disabled
                  :label="$ln('(BingoStar)現金額度')"
                  class="v-input-input"
                /> -->
                <div
                  v-else-if="Items.bingoCashItems.noOpenText === '' && Items.bingoCashItems.disabled"
                  class="royalPrompt">
                  <label class="amountLabel">{{ $ln('(BingoStar)現金額度') }}</label>
                  <div class="amountMessage">{{ Items.bingoCashItems.promptText }}</div>
                </div>
                <v-text-field
                  v-else
                  :placeholder="$ln(Items.bingoCashItems.noOpenText)"
                  disabled
                  :label="$ln('(BingoStar)現金額度')"
                  class="v-input-input"
                />
              </v-flex>

              <!-- (皇家)現金額度 -->
              <v-flex
                v-if="Items.royalCashItems.show"
                xs12
                sm6
                >
                <v-text-field
                  v-if="Items.royalCashItems.noOpenText === '' && !Items.royalCashItems.disabled"
                  v-model.number="Items.royalCashItems.amount"
                  :rules="Items.royalQuotaRules"
                  :suffix="`${Items.royalCashItems.suffixLeft}${Items.royalSurplusQuota}${Items.royalCashItems.suffixRight}`"
                  :placeholder="$ln('額度')"
                  :label="$ln('(皇家)現金額度')"
                  class="v-input-input"
                />
                <!-- <v-text-field
                  v-else-if="Items.royalCashItems.noOpenText === '' && Items.royalCashItems.disabled"
                  v-model="Items.royalCashItems.promptText"
                  :suffix="`${Items.royalCashItems.suffixLeft}${Items.royalSurplusQuota}${Items.royalCashItems.suffixRight}`"
                  disabled
                  :label="$ln('(皇家)現金額度')"
                  class="v-input-input"
                /> -->
                <div
                  v-else-if="Items.royalCashItems.noOpenText === '' && Items.royalCashItems.disabled"
                  class="royalPrompt">
                  <label class="amountLabel">{{ $ln('(皇家)現金額度') }}</label>
                  <div class="amountMessage">{{ Items.royalCashItems.promptText }}</div>
                </div>
                <v-text-field
                  v-else
                  :placeholder="$ln(Items.royalCashItems.noOpenText)"
                  disabled
                  :label="$ln('(皇家)現金額度')"
                  class="v-input-input"
                />
              </v-flex>

              <!-- 單注下限 -->
              <v-flex
                v-if="Items.downLimitItems.show"
                xs12
                sm6>
                <v-text-field
                  v-model.number="Items.downLimitItems.amount"
                  :rules="Items.downLimitItems.rules"
                  :suffix="`≥${Items.downLimitItems.minLimit}`"
                  :disabled="Items.downLimitItems.disabled"
                  :label="$ln('單注下限')"
                  class="v-input-input"
                />
              </v-flex>

              <v-flex
                xs12
                sm6>
                <v-text-field
                  v-model="Items.remark.contents"
                  :rules="Items.remark.contentRules"
                  :label="$ln('備註')"
                />
              </v-flex>

              <!-- 開放比賽類別 -->
              <v-flex xs12>
                <div class="v-input__slot pt-3">
                  <div class="v-text-field__slot">
                    <label
                      aria-hidden="true"
                      class="v-label">{{ $ln('開放比賽類別') }}</label>
                    <v-layout wrap>
                      <v-flex>
                        <v-checkbox
                          class="v-input-checkbox"
                          v-model="openSportGameSelectAll"
                          :label="$ln('全部')"/>
                      </v-flex>
                      <v-flex
                        v-for="(item, i) in Items.openSportGameItems.items"
                        :key="i">
                        <v-layout
                          row
                          warp>
                          <v-flex>
                            <v-checkbox
                              class="v-input-checkbox"
                              v-model="Items.openSportGameItems.selected"
                              :label="$ln(item.name)"
                              :value="item.id"
                              :disabled="item.disabled"
                              number
                            />
                          </v-flex>
                          <v-flex v-if="item.id === 's10'"> <!-- 足球盤 -->
                            <v-select
                              class="v-input-checkbox"
                              v-model="Items.openSportSoccerABCItems.selected"
                              :items="Items.openSportSoccerABCItems.options"
                              :disabled="Items.openSportSoccerABCItems.disabled"
                              style="width: 80px;">
                              <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                              <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                            </v-select>
                          </v-flex>
                          <v-flex v-else-if="item.id === 's15'"> <!-- 世足盤 -->
                            <v-select
                              class="v-input-checkbox"
                              v-model="Items.openSportFifaABCItems.selected"
                              :items="Items.openSportFifaABCItems.options"
                              :disabled="Items.openSportFifaABCItems.disabled"
                              style="width: 80px;">
                              <template slot='selection' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                              <template slot='item' slot-scope='{ item }'>{{ $ln(item.text) }}</template>
                            </v-select>
                          </v-flex>
                        </v-layout>
                      </v-flex>
                    </v-layout>
                  </div>
                </div>
              </v-flex>
            </v-layout>
          </v-form>
        </v-container>
      </v-card-text>

      <v-card-actions>
        <v-spacer/>
        <v-btn
          color="blue darken-1"
          flat
          @click="Items.dialogOpen = false">{{ $ln('取消') }}</v-btn>
        <v-btn
          v-if="Items.valid && Items.account.selected"
          color="deep-orange darken-1"
          @click="add()">{{ $ln('新增') }}</v-btn>
        <v-btn
          v-else
          disabled
          color="deep-orange darken-1">{{ $ln('新增') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Append  from '@/mixins/account-management/level-management/Append.vue'
import { common } from '@/utils'

export default {
  name: 'AppendSuperAccount',
  mixins:[ Append ] ,
  props: {
    upAccount: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
   	  levelName: '會員',
      // 會員層級代號
      level: 1,
      // 上層(代理)層級代號
      upLevel: 2,
      // 路徑
      path:'',
      // 新增資料
      Items: {
        // 開放比賽類別
        openSportGameItems: {
          // (@比賽類別移除外接遊戲，因為已有開放BingoStar和皇家選項設定@)
          // selected: ['s1', 's2', 's3', 's4', 's5', 's6', 's7', 's8', 's9', 's10', 's11', 's12', 's13', 's14', 's15', 'bingo', 'royal'],
          selected: ['s1', 's2', 's3', 's4', 's5', 's6', 's7', 's8', 's9', 's10', 's11', 's12', 's13', 's14', 's15'],
          items: [
            { id: 's1', name: '美棒', disabled: false },
            { id: 's2', name: '日棒', disabled: false },
            { id: 's3', name: '台棒', disabled: false },
            { id: 's4', name: '韓棒', disabled: false },
            { id: 's5', name: '冰球', disabled: false },
            { id: 's6', name: '籃球', disabled: false },
            { id: 's7', name: '彩球', disabled: false },
            { id: 's8', name: '美足', disabled: false },
            { id: 's9', name: '網球', disabled: false },
            { id: 's10', name: '足球', disabled: false },
            { id: 's11', name: '指數', disabled: false },
            { id: 's12', name: '賽馬＆賽狗', disabled: false },
            { id: 's13', name: '電競', disabled: false },
            { id: 's14', name: '其他', disabled: false },
            { id: 's15', name: '世足', disabled: false }
            // { id: 'bingo', name: 'BingoStar', disabled: false }, // (@比賽類別移除外接遊戲，因為已有開放BingoStar和皇家選項設定@)
            // { id: 'royal', name: '皇家', disabled: false } // (@比賽類別移除外接遊戲，因為已有開放BingoStar和皇家選項設定@)
          ]
        },
        // 開放[足球]比賽類別-ABC盤(1: A盤, 2: B盤, 3: C盤)
        openSportSoccerABCItems: {
          show: true,
          selected: 3,
          disabled: false,
          options: [
            { text: 'A盤', value: 1, disabled: false },
            { text: 'B盤', value: 2, disabled: false },
            { text: 'C盤', value: 3, disabled: false }
          ]
        },
        // 開放[世足]比賽類別-ABC盤(1: A盤, 2: B盤, 3: C盤)
        openSportFifaABCItems: {
          show: true,
          selected: 3,
          disabled: false,
          options: [
            { text: 'A盤', value: 1, disabled: false },
            { text: 'B盤', value: 2, disabled: false },
            { text: 'C盤', value: 3, disabled: false }
          ]
        }
      }
    }
  },
  computed: {
    // 開放比賽類別-全選
    openSportGameSelectAll: {
      get: function () {
        let selectCount = 0
        for (let i in this.Items.openSportGameItems.items) {
          if (!this.Items.openSportGameItems.items[i].disabled) selectCount++
        }
        return this.Items.openSportGameItems.items ? this.Items.openSportGameItems.selected.length === selectCount : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.Items.openSportGameItems.items) {
            if (!this.Items.openSportGameItems.items[i].disabled) selected.push(this.Items.openSportGameItems.items[i].id)
          }
        }
        this.Items.openSportGameItems.selected = selected
      }
    }
  },
  watch: {
    getGenerateGleadAccountState (payload) {
      this.Items.account.selected = payload.result.account[0]
      this.Items.account.options = payload.result.account
    },
    // 依照「開放BingoStar」狀態 disable / enabled 額度輸入欄及拆帳百分比
    'Items.openBingo.selected' (status) {
      this.Items.bingoCreditItems.disabled = status === 1
      this.Items.bingoCashItems.disabled = status === 1
    },
    // 依照「開放真人(皇家)類別」狀態 disable / enabled 額度輸入欄及拆帳百分比
    'Items.openRoyal.selected' (status) {
      this.Items.royalCreditItems.disabled = status === 1
      this.Items.royalCashItems.disabled = status === 1
    },
    'Items.dialogOpen' (dialogOpen) {
      // 關閉新增帳號彈窗
      if (!dialogOpen) {
        // 清除欄位資料
        this.clearField()
        // 關閉彈窗 reset 驗證功能
        this.$refs.form.resetValidation()

        /* 重新刷新資料 */
        // 刷新自動生成帳號編號(管端帳號)
        this.loadGenerateAccount()
        // 取得預設資料
        this.getDefault()
        // 載入[BingoStar]上層帳號資訊
        this.loadUpAccountBingoStar()
        // 載入[Super彩球]上層帳號資訊
        this.loadUpAccountSuperLottery()
        // 載入[皇家]上層帳號資訊
        this.loadUpAccountRoyal()
        // 載入上層帳號資訊
        this.loadUpAccount()
      }
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  mounted () {
    // 路徑
    let temp = (this.$route.path).split('/')
    this.path = `/manage/${temp[2]}/${temp[3]}/`

    // 取得自動生成帳號編號(管端帳號)
    this.loadGenerateAccount()
    // 取得預設資料
    this.getDefault()
    // 載入[BingoStar]上層帳號資訊
    this.loadUpAccountBingoStar()
    // 載入[Super彩球]上層帳號資訊
    this.loadUpAccountSuperLottery()
    // 載入[皇家]上層帳號資訊
    this.loadUpAccountRoyal()
    // 載入上層帳號資訊
    this.loadUpAccount()

    // 依照「開放BingoStar」狀態 disable / enabled 額度輸入欄
    this.Items.bingoCreditItems.disabled = this.Items.openBingo.selected === 1
    this.Items.bingoCashItems.disabled = this.Items.openBingo.selected === 1

    // 依照「開放真人(皇家)類別」狀態 disable / enabled 額度輸入欄
    this.Items.royalCreditItems.disabled = this.Items.openRoyal.selected === 1
    this.Items.royalCashItems.disabled = this.Items.openRoyal.selected === 1

    this.updateDashboardInfo()
  },
  methods: {
    add () {
      let addData = {
        l_no: this.Items.account.selected,
        l_name: this.Items.nameItems.name,
        l_pwd: this.Items.passwordItems.password,
        l_pwd_confirmation: this.Items.passwordItems.passwordConfirmation,
        l_status: this.Items.whetherAllowLogin.selected,
        l_close: this.Items.whetherAllowBet.selected,
        remark: this.Items.remark.contents
      }
      // 上層帳號
      if (this.getProfileState.hasOwnProperty('result') && this.getProfileState.result !== undefined) {
        // 若登入帳號不為代理，則需要送上層帳號；若登入帳號為代理，則送登入帳號
        addData.upAccount = this.getProfileState.result.l_lv !== 2 ? this.upAccount : sessionStorage.getItem('super.manage.account')
      }
      // (體育)信用額度
      if (this.Items.sportCreditItems.show && !this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCreditItems.amount
      // (體育)現金額度
      } else if (!this.Items.sportCreditItems.show && this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCashItems.amount
      }
      // (BingoStar)信用額度
      if ((this.Items.bingoCreditItems.show && !this.Items.bingoCreditItems.disabled) &&
          !this.Items.bingoCashItems.show &&
          this.Items.bingoCreditItems.noOpenText === '') {
        addData.quota_bingo = this.Items.bingoCreditItems.amount
      // (BingoStar)現金額度
      } else if (!this.Items.bingoCreditItems.show &&
                 (this.Items.bingoCashItems.show && !this.Items.bingoCashItems.disabled) &&
                 this.Items.bingoCashItems.noOpenText === '') {
        addData.quota_bingo = this.Items.bingoCashItems.amount
      }
      // (皇家)信用額度
      if ((this.Items.royalCreditItems.show && !this.Items.royalCreditItems.disabled) &&
          !this.Items.royalCashItems.show &&
          this.Items.royalCreditItems.noOpenText === '') {
        addData.quota_royal = this.Items.royalCreditItems.amount
      // (皇家)現金額度
      } else if (!this.Items.royalCreditItems.show &&
                 (this.Items.royalCashItems.show && !this.Items.royalCashItems.disabled) &&
                 this.Items.royalCashItems.noOpenText === '') {
        addData.quota_royal = this.Items.royalCashItems.amount
      }
      // 單注下限
      if (this.Items.downLimitItems.show) {
        addData.down_limit = this.Items.downLimitItems.amount
      }
      // 開放BingoStar
      if (this.Items.openBingo.show && !this.Items.openBingo.disabled) addData.bingo_status = this.Items.openBingo.selected
      // 開放Super彩球
      if (this.Items.openSuperLottery.show && !this.Items.openSuperLottery.disabled) addData.lottery_status = this.Items.openSuperLottery.selected
      // 開放真人(皇家)類別
      if (this.Items.openRoyal.show && !this.Items.openRoyal.disabled) addData.royal_status = this.Items.openRoyal.selected

      // 開放比賽類別
      for (let i in this.Items.openSportGameItems.items) {
        if (this.Items.openSportGameItems.selected.indexOf(this.Items.openSportGameItems.items[i].id) !== -1) {
          // 開放送 0
          if (!this.Items.openSportGameItems.items[i].disabled) addData[`g_open${this.Items.openSportGameItems.items[i].id.split('s')[1]}`] = 0
        } else {
          // 未開放送 1
          if (!this.Items.openSportGameItems.items[i].disabled) addData[`g_open${this.Items.openSportGameItems.items[i].id.split('s')[1]}`] = 1
        }
      }
      // 開放[足球]ABC盤
      if (this.Items.openSportSoccerABCItems.show && !this.Items.openSportSoccerABCItems.disabled) addData.g_fb = this.Items.openSportSoccerABCItems.selected
      // 開放[世足]ABC盤(未開放)
      // if (this.Items.openSportFifaABCItems.show && !this.Items.openSportFifaABCItems.disabled) addData.g_fifa = this.Items.openSportSoccerABCItems.selected
      try {
        this.postSuperAccount(addData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`新增失敗`))
            } else if (res.status === 200) { // success
              this.$toastr.success(this.$ln(`新增成功`), '', {timeOut: 2000, closeButton: false})
              // 新增成功狀態
              this.$emit('status', true)
              // 關閉彈窗
              this.Items.dialogOpen = false
              if(this.upLevel == sessionStorage.getItem('super.manage.level')){
                this.$router.push(`${this.path}directList`);
                this.$router.push(`${this.path}edit?account=${addData.l_no}`)
              }else{
                this.$router.push(`${this.path}?upAccount=${this.upAccount}`);
                this.$router.push(`${this.path}edit?account=${addData.l_no}&upAccount=${this.upAccount}`)
              }
            }
          })
          .catch(err => {
            this.$toastr.error(this.$ln(`新增失敗`))
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 取得上層帳號資訊
    loadUpAccount () {
      try {
        // 登入帳號為代理，則送登入帳號(上層帳號)，否則送選單帳號(上層帳號)
        let account = parseInt(sessionStorage.getItem('super.manage.level')) === 2 ? sessionStorage.getItem('super.manage.account') : this.upAccount
        this.getUpAccount({ balltype: 1, l_no: account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`會員管理-上層帳號(${account})資訊:`, res.data.result)
                // 設置單注下限
                this.Items.downLimitItems.amount = res.data.result.down_limit
                this.Items.downLimitItems.minLimit = res.data.result.down_limit

                // 設置開放比賽類別
                let getOpenGame = []
                for (let key in res.data.result.status) {
                  if (res.data.result.status[key] === 0) getOpenGame.push('s' + key)
                }
                this.Items.openSportGameItems.selected = getOpenGame
                // disable 上層不開放的比賽類別
                for (let i in this.Items.openSportGameItems.items) {
                  this.Items.openSportGameItems.items[i].disabled = this.Items.openSportGameItems.selected.indexOf(this.Items.openSportGameItems.items[i].id) === -1
                  // 足球ABC盤
                  this.Items.openSportSoccerABCItems.disabled = this.Items.openSportGameItems.selected.indexOf('s10') === -1
                  // 世足ABC盤
                  this.Items.openSportFifaABCItems.disabled = this.Items.openSportGameItems.selected.indexOf('s15') === -1
                }
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    /**
     * 不顯示球類遊戲的開放比賽類別選項
     * @params type  球類遊戲代號
     **/
    noShowOpenGameItems (type) {
      // 不顯示 ${type} 開放比賽類別 items
      let newOpenSportGameStarItems = []
      for (let i in this.Items.openSportGameItems.items) {
        if (this.Items.openSportGameItems.items[i].id !== type) newOpenSportGameStarItems.push(this.Items.openSportGameItems.items[i])
      }
      this.Items.openSportGameItems.items = newOpenSportGameStarItems
      // 不顯示 ${type} 開放比賽類別 selected
      let openSportGameStarSelectedItems = []
      for (let i in this.Items.openSportGameItems.selected) {
        if (this.Items.openSportGameItems.selected[i] !== type) openSportGameStarSelectedItems.push(this.Items.openSportGameItems.selected[i])
      }
      this.Items.openSportGameItems.selected = openSportGameStarSelectedItems

      // 不顯示[世足]ABC盤
      if (type === 's15') this.Items.openSportFifaABCItems.show = false
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo &&
          dashboardInfo.hasOwnProperty('wsType') &&
          dashboardInfo.hasOwnProperty('noOpenGame')) {
        /* 註: 先更新站台種類設置再更新開放遊戲設定 */
        // 依照站台種類更新輸入欄(1:現金、2:信用)
        switch (dashboardInfo.wsType) {
          case 1:
            this.Items.sportCashItems.show = true
            this.Items.bingoCashItems.show = true
            this.Items.royalCashItems.show = true
            this.Items.sportCreditItems.show = false
            this.Items.bingoCreditItems.show = false
            this.Items.royalCreditItems.show = false
            break
          case 2:
            this.Items.sportCreditItems.show = true
            this.Items.bingoCreditItems.show = true
            this.Items.royalCreditItems.show = true
            this.Items.sportCashItems.show = false
            this.Items.bingoCashItems.show = false
            this.Items.royalCashItems.show = false
            break
        }
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉世足
        case 's15':
          // 不顯示[世足]開放比賽類別選項
          this.noShowOpenGameItems('s15')
          break
        // 關閉BingoStar
        case 's16':
          this.Items.bingoCashItems.show = false
          this.Items.bingoCreditItems.show = false
          this.Items.openBingo.show = false

          // 不顯示[BingoStar]開放比賽類別選項 (@比賽類別移除外接遊戲，因為已有開放BingoStar和皇家選項設定@)
          // this.noShowOpenGameItems('bingo')
          break
        // 關閉Super彩球
        case 's17':
          this.Items.openSuperLottery.show = false
          break
        // 關閉皇家視訊
        case 's18':
          this.Items.royalCashItems.show = false
          this.Items.royalCreditItems.show = false
          this.Items.openRoyal.show = false

          // 不顯示[皇家]開放比賽類別選項 (@比賽類別移除外接遊戲，因為已有開放BingoStar和皇家選項設定@)
          // this.noShowOpenGameItems('royal')
          break
      }
    }
  }
}
</script>

